
.background {
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    min-height: 100%;
    height: auto;
    z-index: 102;
    background: rgba(0, 0, 0, .2);
}
.colorbox {
    position: absolute;
    top: 0px;
    right: 0px;
    z-index: 103;
    width: 100%;
    height: auto;
    /*box-shadow: rgb(223, 223, 223) 1px 3px 3px;*/
    margin-top: 30px;
    border-width: 1px;
    border-style: solid;
    border-color: rgb(175, 175, 175);
    -o-border-image: initial;
       border-image: initial;
    border-radius: 3px;
    background-color: #fff;
}
.arrow-outer {
    z-index: 102;
    width: 12px;
    height: 8px;
    position: absolute;
    top: -7px;
    right: 32px;
    overflow: hidden;
}
.arrow {
    border: 1px solid #AFAFAF;
    width: 10px;
    height: 10px;
    position: relative;
    top: 2px;
    -webkit-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    background: #FFFFFF;
    -moz-transform: rotate(45deg);
}



