
.login-dialog {
	    position: fixed;
        top: 0px;
        z-index: 102;
	    width: 100%;
	    min-height: 100%;
	    height: auto;
	    background-color: rgba(0, 0, 0, .5);
}
.login-content {
	    position: absolute;
        top: 50px;
        bottom: 50px;
        margin: 0px 4%;
        width: 92%;
        min-height: 450px;
	    border-radius: 8px;
	    overflow: scroll;
	    background-color: #fff;
}

	
