
.signIn {
	position: absolute;
	top: 0px;
	left: 0px;
	width: 100%;
	min-height: 100%;
	height: auto;
	background: url(../../assets/img/bg2.jpg);
	background-size: 100% 100%;
}
.login-container {
	position: absolute;
	top: 80px;
	margin: 0px 5%;
	width: 90%;
}
.input-group {
	background-color: rgba(255,255,255,.6);
}
.login-btn {
	margin: 30px auto;
	opacity: .6;
}
.login-footer {
	text-align: center;
	margin-top: 66px;
	color: #2196F3;
}
.to-signUp, .to-signIn, .lost-pass {
	cursor: pointer;
}
.line {
	margin: 0 10px;
}



